const { basis } = require('basis');

import { game } from './game.js'
import { interact } from './interact.js'

cc.Class({
    extends: cc.Component,

    properties: {
        tx: cc.Sprite,
        mingzi: cc.Label,
        jinbi: cc.Label,
        mingci: cc.Label,
        jiangbei: cc.Label,
        mingci1:cc.Node,
        mingci2:cc.Node,
        mingci3:cc.Node,
    },
    init(index) {
        this.mingci.string = index + 1;
        if(index == 0){
            this.mingci1.active = true;
        }else if(index == 1){
            this.mingci2.active = true;
        }else if(index == 2){
            this.mingci3.active = true;
        }else{
            this.mingci.node.active = true
        }
        this.jiangbei.string = basis.gameRankReward[index];
        this.mingzi.string = interact._this.goldRnak[index].gameName;
        this.jinbi.string = Math.round(interact._this.goldRnak[index].gold);

        if (interact._this.goldRnak[index].gameName == basis.gameNmae) {
            this.node.getComponent(cc.Sprite).spriteFrame = interact._this.gameImgList.getSpriteFrame('自己di')
        }
    }
});
